import React, {Component} from 'react';
import {withRouter, Switch, Route} from 'react-router-dom';
import {connect} from 'react-redux';
import Layout from './Layout/Layout';
import 'antd/dist/antd.css';
import './App.css';
import Task from "./containers/Task";

class App extends Component {
    render() {
        return (
            <Layout>
                <Switch>

                    <Route
                        exact
                        path={'/collaborators'}
                        render={() => <div>i am collaborators</div>}
                    />
                    <Route exact path={'/timeline'} render={() => <div>i am time line</div>}/>
                    <Route exact path={'/tasks'} render={Task}/>
                    <Route exact path={'/statistics'} render={() => <div>i am statistics</div>}/>
                    <Route exact path={'/sprints'} render={() => <div>i am sprint meetings</div>}/>
                    <Route exact path={'/remotegit'} render={() => <div>i am remote git</div>}/>
                    <Route exact path={'/settings'} render={() => <div>i am settings</div>}/>
                    <Route
                        exact
                        path={'/notifications'}
                        render={() => <div>i am notification</div>}
                    />
                    <Route exact path={'/'} render={() => <div>I am dashboard</div>}/>
                </Switch>
            </Layout>
        );
    }
}

export default withRouter(connect()(App));
