import * as Constants from "../constants/TaskConstants";

const initialState = {
    tasks: [],
    isFetchingAllTasks: "",
    isFetchingTask: "",
    isPostingEditedTask: "",
    dateReceived: "",
    getError: "",
    errorText: "",
    task: [],
    taskForm: []
};

export const Task = (state = initialState, action) => {
    switch (action.type) {
        case Constants.REQUEST_ALL_TASKS:
            return {
                ...state,
                isFetchingAllTasks: true,
                isFetchingTask: false,
                isPostingEditedTask: false,
                getError: false,
            };
        case Constants.RECEIVE_ALL_TASKS:
            return {
                ...state,
                isFetchingAllTasks: false,
                isFetchingTask: false,
                isPostingEditedTask: false,
                getError: false,
                tasks: action.data,
                receiveAt: action.receiveAt
            };
        case Constants.FAILED_RECEIVE_ALL_TASKS:
            return {
                ...state,
                isFetchingAllTasks: false,
                isFetchingTask: false,
                isPostingEditedTask: false,
                getError: true,
                errorText: action.err
            };
        default:
            return state;
    }
};
