import React, {Component, Fragment} from "react";
import {Spin, Icon, Row, Col, Select, message, AutoComplete} from 'antd';
import {Modal, Button} from 'antd';
import {Input} from 'antd';
import {fetchAllTasks} from "../../actions/TasksAction";
import {connect} from "react-redux";
import {withRouter} from "react-router";

const {TextArea} = Input;
const InputGroup = Input.Group;
const {Option} = Select;
const antIcon = <Icon type="loading" style={{fontSize: 40}} spin/>;


class Tasks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isFetchedData: true,
            loading: false,
            visible: false,
            taskId: "",
            taskBacklogText: "",
            taskPicker: "",
            taskBacklog: "",
            taskTitle: "",
            taskDescription: "",
            showEditor: false,
            taskState: "",
            selectedBacklog: [],
            backlogs: [],
            mainData: [],
            dataSource: []

        };
        this.TaskState = {
            "TO_DO": "انجام نشده",
            "IN_PROGRESS": "در حال انجام",
            "DONE": "انجام شده"
        };
    }

    componentDidMount() {
        const {dispatch} = this.props;
        dispatch(fetchAllTasks());
        return fetch(`https://mamaly100.pythonanywhere.com/Backlog/`, {
            method: "GET",
            headers: {'Content-Type': 'application/json'},
        })
            .then(res => res.json())
            .then(data => {
                let newMainData = [];
                let newdataSource = [];
                data.map(item => {
                    newMainData.push(item['name']);
                    newdataSource.push(item['name']);
                    return null;
                });
                this.setState({
                    mainData: newMainData,
                    dataSource: newdataSource,
                    backlogs: data
                });
            })
    }

    showSuccessMessage = () => {
        message.success('تغییرات با موفقیت اعمال شد :)');
    };

    showErrorMassege = () => {
        message.error('خطا در ارتباط با سرور');
    };

    showModal = async (id) => {
        let selectedTask = this.props.tasks.filter(item => item["id"] === id);
        this.setState({
            fetchingBacklog: true,
            visible: true,
        });

        let res = await fetch(`https://mamaly100.pythonanywhere.com/Backlog/${selectedTask[0]["BackLogID"]}`, {
            method: "GET",
            headers: {'Content-Type': 'application/json'},
        })
            .then(res => res.json())
            .then(data => {
                this.setState({
                    selectedBacklog: data,
                    fetchingBacklog: false
                })
            });
        this.setState({
            taskTitle: selectedTask[0]["title"],
            taskId: id,
            taskPicker: selectedTask[0]["picker"],
            taskBacklog: selectedTask[0]["BackLogID"],
            taskDescription: selectedTask[0]["description"],
            taskState: selectedTask[0]["TaskState"],
            taskBacklogText: this.state.selectedBacklog["name"]
        });
    };

    handleOk = () => {
        this.setState({loading: true});
        return fetch(`https://mamaly100.pythonanywhere.com/Task/${this.state.taskId}/`, {
            method: "PUT",
            body: JSON.stringify({
                "id": this.state.taskId,
                "title": this.state.taskTitle,
                "description": this.state.taskDescription,
                "TaskState": this.state.taskState,
                "picker": this.state.taskPicker,
                "BackLogID": this.state.taskBacklog
            }),
            headers: {'Content-Type': 'application/json'},
        })
            .then(res => res.json())
            .then(data => {
                this.setState({loading: false, visible: false});
                this.props.dispatch(fetchAllTasks());
                this.showSuccessMessage();
            })
            .catch(err => this.showErrorMassege());
    };

    handleCancel = () => {
        this.setState({visible: false, showEditor: false});
    };

    _renderTasks = () => {
        return (
            <div className={"task-wrapper p-4 mb-5"}>
                <Row gutter={24} className={"d-flex flex-row-reverse"}>
                    <Col span={8}>
                        <div className={"task-done-wrapper-"}>
                            <div className={"task-done-wrapper-header"}>
                                <h4>انجام شده ها</h4>
                            </div>
                            <div className={"task-done-wrapper-body"}>
                                {this.props.tasks.map((item, index) => {
                                    if (item["TaskState"] === "DONE") {
                                        return (
                                            <div
                                                onClick={() => this.showModal(item.id)}
                                                className={"task-done-wrapper-body-item m-4"} key={item.id}>
                                                <h3>{item.title}</h3>
                                                <p className={"mt-3 fs-12 project-task-description"}>{item.description}</p>
                                            </div>
                                        )
                                    }
                                    return null
                                })}
                            </div>
                        </div>
                    </Col>
                    <Col span={8}>
                        <div className={"task-in-progress-wrapper"}>
                            <div className={"task-undone-wrapper-header"}>
                                <h4>در جریان</h4>
                            </div>
                            <div className={"task-undone-wrapper-body"}>
                                {this.props.tasks.map((item, index) => {
                                    if (item["TaskState"] === "IN_PROGRESS") {
                                        return (
                                            <div
                                                onClick={() => this.showModal(item.id)}
                                                className={"task-inProgress-wrapper-body-item m-4"} key={item.id}>
                                                <h3>{item.title}</h3>
                                                <p className={"mt-3 fs-12"}>{item.description}</p>
                                            </div>
                                        )
                                    }
                                    return null
                                })}
                            </div>
                        </div>
                    </Col>
                    <Col span={8}>
                        <div className={"task-done-wrapper-"}>
                            <div className={"task-done-wrapper-header"}>
                                <h4>انجام نشده ها</h4>
                            </div>
                            <div className={"task-undone-wrapper-body"}>
                                {this.props.tasks.map((item, index) => {
                                    if (item["TaskState"] === "TO_DO") {
                                        return (
                                            <div
                                                onClick={() => this.showModal(item.id)}
                                                className={"task-undone-wrapper-body-item m-4"} key={item.id}>
                                                <h3>{item.title}</h3>
                                                <p className={"mt-3 fs-12"}>{item.description}</p>
                                            </div>
                                        )
                                    }
                                    return null;
                                })}
                            </div>
                        </div>
                    </Col>
                </Row>
                <div className={"last-data-fetched-time"}>
                    <p>
                        {this.props.receiveAt}
                        <span className={"fs-10"}>آخرین به روز رسانی</span>
                    </p>
                </div>
            </div>
        )
    };

    _renderSpinner = () => {
        return (
            <div className={"task-done-wrapper"}>
                <Spin indicator={antIcon}/>
            </div>
        )
    };

    toggleEditor = () => {
        this.setState({
            showEditor: !this.state.showEditor
        })
    };

    onSelect = async (value) => {
        let selectedBacklog = await this.state.backlogs.filter(item => item["name"] === value);
        this.setState({
            taskBacklog: selectedBacklog[0]["id"],
            taskBacklogText: value
        });
    };

    handleSearch = value => {
        let newDataSource;
        newDataSource = this.state.mainData.filter((item) => {
            return item.indexOf(value) !== -1
        });
        this.setState({
            dataSource: newDataSource
        });
    };

    _renderEditor = () => {
        return (
            <div dir={"rtl"}>
                <h3 className={"mb-1"}>
                    عنوان
                </h3>
                <Input defaultValue={this.state.taskTitle}
                       className={"text-justify fs-12 mb-1"}
                       onChange={(e) => this.setState({taskTitle: e.target.value})}/>
                <h3 className={"mb-1"}>
                    توضیحات
                </h3>
                <TextArea rows={4} defaultValue={this.state.taskDescription}
                          className={"text-justify fs-12"}
                          onChange={(e) => this.setState({taskDescription: e.target.value})}/>
                <h3 className={"mt-3 mb-1"}>
                    انتخاب کننده
                </h3>
                <p className={"fs-12"}>
                    نامشخص
                </p>
                <h3 className={"mt-3 mb-1"}>
                    بکلاگ
                </h3>
                <AutoComplete
                    dataSource={this.state.dataSource}
                    style={{width: '100%'}}
                    onSelect={this.onSelect}
                    onSearch={this.handleSearch}
                    placeholder="بکلاگ مورد نظر را انتخاب کنید"
                />
                <h3 className={"mt-3 mb-1"}>
                    وضعیت
                </h3>
                <InputGroup compact>
                    <Select defaultValue="وضعیت خود را انتخاب کنید..." onChange={e => {
                        this.setState({
                            taskState: e,
                        })

                    }}
                            style={{width: "100%"}}>
                        <Option value="DONE">انجام شده</Option>
                        <Option value="TO_DO">انجام نشده</Option>
                        <Option value="IN_PROGRESS">در جریان</Option>
                    </Select>
                </InputGroup>
            </div>
        )
    };

    _renderTaskDetails = () => {
        return (
            <div dir={"rtl"}>
                <h3 className={"mb-1"}>
                    توضیحات
                </h3>
                <p className={"text-justify fs-12"}>
                    {this.state.taskDescription}
                </p>

                <h3 className={"mt-3 mb-1"}>
                    انتخاب کننده
                </h3>
                <p className={"fs-12"}>
                    نامشخص
                </p>
                <h3 className={"mt-3 mb-1"}>
                    بکلاگ
                </h3>
                <p className={"fs-12"}>
                    {this.state.taskBacklogText}
                </p>
                <h3 className={"mt-3 mb-1"}>
                    وضعیت
                </h3>
                <p className={"fs-12"}>
                    {this.state.taskState === "IN_PROGRESS" ? "در جریان" : this.state.taskState === "TO_DO" ? "انجام نشده" : "انجام شده"}
                </p>
            </div>
        )
    };

    render() {
        return (
            <Fragment>
                <Modal
                    className={"tasks-modal-custom"}
                    visible={this.state.visible}
                    title={<p className={"modal-header-title-text"}>{this.state.taskTitle}</p>
                    }
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    footer={[
                        <Button key="back" onClick={this.handleCancel}>
                            بازگشت
                        </Button>,
                        <Button key="submit" type="primary" loading={this.state.loading} onClick={this.handleOk}>
                            اعمال
                        </Button>,
                        <Button key="edit" type="primary" onClick={this.toggleEditor}>
                            {this.state.showEditor ? "اعمال تغییرات" : "ویرایش"}
                        </Button>,
                    ]}
                >
                    <div>
                        {this.state.showEditor ? this._renderEditor() : this.state.fetchingBacklog ?
                            <div className={"d-flex justify-content-center"}><Spin/></div> : this._renderTaskDetails()}
                    </div>

                </Modal>
                {this.props.isFetchingAllTasks ? this._renderSpinner() : this._renderTasks()}
            </Fragment>
        )
    }
}

const mapStateToProps = (state) => {
    return state.Task;
};

export default withRouter(connect(mapStateToProps, null)(Tasks));