import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {withRouter} from "react-router";
import {Layout as AntLayout, Menu, Icon} from 'antd';
import {fa} from '../assets/locales/fa';


const {Header, Sider, Content} = AntLayout;

class Layout extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collapsed: false,
            defaultKey: "1"
        };
        this.defaultKey = "1";
    }


    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed
        });
    };

    render() {
        switch (this.props.history.location.pathname) {
            case "/":
                this.defaultKey = "1";
                break;
            case "/collaborators":
                this.defaultKey = "2";
                break;
            case "/timeline":
                this.defaultKey = "3";
                break;
            case "/tasks":
                this.defaultKey = "4";
                break;
            case "/statistics":
                this.defaultKey = "5";
                break;
            case "/sprints":
                this.defaultKey = "6";
                break;
            case "/remotegit":
                this.defaultKey = "7";
                break;
            case "/settings":
                this.defaultKey = "8";
                break;
            case "/notifications":
                this.defaultKey = "9";
                break;
            default:
        }
        return (
            <AntLayout className={"ant-design-layout-ui-custom"} direction={"rtl"}>
                <Sider trigger={null} className={"ant-design-slider"} collapsible collapsed={this.state.collapsed}>

                    <div style={{height: "100%"}}>

                        <div className={"sidebar-menu-wrapper"} style={{height: "100%"}}>
                            <div className={"thin-side-bar"}>
                                <Icon type="home"
                                      className={"ant-design-ui-custom-thin-sidebar-item-icon"}/>
                                <Icon type="wechat"
                                      className={"ant-design-ui-custom-thin-sidebar-item-icon"}/>
                                <Icon type="notification"
                                      className={"ant-design-ui-custom-thin-sidebar-item-icon"}/>
                                <Icon type="search"
                                      className={"ant-design-ui-custom-thin-sidebar-item-icon"}/>
                            </div>

                            <Menu theme="light" mode="inline" defaultSelectedKeys={[this.defaultKey]}>
                                <div className="logo">
                                    <p style={{display: this.state.collapsed ? "none" : "block"}}>
                                        نرم افزار مدیریت پروژه
                                    </p>
                                </div>
                                <Menu.Item key="1">
                                    <Link to='/'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.DASHBOARD}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="2">
                                    <Link to='/collaborators'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}> {fa.COLLABORATORS} </span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="3">
                                    <Link to='/timeline'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.TIMELINE}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="4">
                                    <Link to='/tasks'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}> {fa.TASK} </span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="5">
                                    <Link to='/statistics'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.STATISTICS}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="6">
                                    <Link to='/sprints'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.SPRINTS}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="7">
                                    <Link to='/remotegit'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.REMOTEGIT}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="8">
                                    <Link to='/settings'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.SETTINGS}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="9">
                                    <Link to='/notifications'>
                                        <div>
                                            <Icon type='video-camera'
                                                  className={"ant-design-ui-custom-sidebar-item-icon"}/>
                                            <span className={"fs-12"}>{fa.NOTIFICATIONS}</span>
                                        </div>
                                    </Link>
                                </Menu.Item>
                            </Menu>
                        </div>
                    </div>
                </Sider>
                <AntLayout>
                    <Header
                        style={{
                            background: '#fff',
                            padding: '0px',
                        }}
                        className={"ant-design-header-ui-custom"}
                    >
                        <Icon
                            className='trigger'
                            type={this.state.collapsed ? 'menu-fold' : 'menu-unfold'}
                            onClick={this.toggle}
                        />
                    </Header>
                    <Content
                        style={{
                            margin: '24px 16px',
                            padding: 24,
                            background: '#fff',
                            minHeight: 280
                        }}
                    >
                        {this.props.children}
                    </Content>
                </AntLayout>

            </AntLayout>
        );
    }
}


export default withRouter(Layout);
