import * as Constants from "../constants/TaskConstants";

// ------------- GET ALL TASKS -------------

const requestAllTasks = () => ({
    type: Constants.REQUEST_ALL_TASKS
});

const receiveAllTasks = (json) => ({
    type: Constants.RECEIVE_ALL_TASKS,
    data: json,
    receiveAt: new Date().toString().substring(15, 25)
});

const failedReceiveAllTasks = (err) => ({
    type: Constants.FAILED_RECEIVE_ALL_TASKS,
    err
});

export const fetchAllTasks = () => {
    return dispatch => {
        dispatch(requestAllTasks());
        return fetch(`https://mamaly100.pythonanywhere.com/Task/`, {
            method: "GET",
            headers: new Headers({
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            })
        })
            .then(response => response.json())
            .then(data => dispatch(receiveAllTasks(data)))
            .catch(err => dispatch(failedReceiveAllTasks(err)))
    }
};

