import React, {Component} from "react";
import {Input, Col, Row, Select, message, Form, Button, AutoComplete} from 'antd';

const InputGroup = Input.Group;
const {Option} = Select;
const {TextArea} = Input;


class CreateTask extends Component {

    constructor(props) {
        super(props);
        this.state = {
            taskTitle: "",
            taskBacklog: "",
            taskDescription: "",
            taskState: "",
            loading: false,
            taskTitleValidating: {
                validateStatus: "",
                help: ""
            },
            taskBacklogsValidating: {
                validateStatus: "",
                help: ""
            },
            taskDescriptionValidating: {
                validateStatus: "",
                help: ""
            },
            taskStateValidating: {
                validateStatus: "",
                help: ""
            },
            backlogs: [],
            mainData: [],
            dataSource: []
        }
    }

    componentDidMount() {
        return fetch(`https://mamaly100.pythonanywhere.com/Backlog/`, {
            method: "GET",
            headers: {'Content-Type': 'application/json'},
        })
            .then(res => res.json())
            .then(data => {
                let newMainData = [];
                let newdataSource = [];
                data.map(item => {
                    newMainData.push(item['name']);
                    newdataSource.push(item['name']);
                    return null;
                });
                this.setState({
                    mainData: newMainData,
                    dataSource: newdataSource,
                    backlogs: data
                });
            })
    }

    handleSearch = value => {
        let newDataSource;
        newDataSource = this.state.mainData.filter((item) => {
            return item.indexOf(value) !== -1
        });
        this.setState({
            dataSource: newDataSource
        });
    };

    onSelect = async (value) => {
        let selectedBacklog = await this.state.backlogs.filter(item => item["name"] === value);
        this.setState({
            taskBacklog: selectedBacklog[0]["id"]
        });
    };

    showSuccessMessage = () => {
        message.success('وظیفه با موفقیت اضافه شد :)');
    };

    showErrorMassege = () => {
        message.error('خطا در ارتباط با سرور');
    };

    taskTitleValidating = () => {
        if (this.state.taskTitle.length === 0) {
            let newTaskTitleValidating = {
                validateStatus: "error",
                help: "پر کردن این فیلد الزامی است"
            };
            this.setState({
                taskTitleValidating: newTaskTitleValidating
            });
            return false;
        } else {
            let newTaskTitleValidating = {
                validateStatus: "success",
                help: ""
            };
            this.setState({
                taskTitleValidating: newTaskTitleValidating
            });
            return true;
        }
    };

    taskBacklogsValidating = () => {
        if (this.state.taskBacklog.length === 0) {
            let newTaskBacklogsValidating = {
                validateStatus: "error",
                help: "پر کردن این فیلد الزامی است"
            };
            this.setState({
                taskBacklogsValidating: newTaskBacklogsValidating
            });
            return false;
        } else {
            let newTaskBacklogsValidating = {
                validateStatus: "success",
                help: ""
            };
            this.setState({
                taskBacklogsValidating: newTaskBacklogsValidating
            });
            return true;
        }
    };

    taskDescriptionValidating = () => {
        if (this.state.taskDescription.length === 0) {
            let newTaskDescriptionValidating = {
                validateStatus: "error",
                help: "پر کردن این فیلد الزامی است"
            };
            this.setState({
                taskDescriptionValidating: newTaskDescriptionValidating
            });
            return false;
        } else {
            let newTaskDescriptionValidating = {
                validateStatus: "success",
                help: ""
            };
            this.setState({
                taskDescriptionValidating: newTaskDescriptionValidating
            });
            return true;
        }
    };

    taskStateValidating = () => {
        if (this.state.taskState.length === 0) {
            let newTaskStateValidating = {
                validateStatus: "error",
                help: "پر کردن این فیلد الزامی است"
            };
            this.setState({
                taskStateValidating: newTaskStateValidating
            });
            return false
        } else {
            let newTaskStateValidating = {
                validateStatus: "success",
                help: ""
            };
            this.setState({
                taskStateValidating: newTaskStateValidating
            });
            return true
        }
    };

    checkValidating = () => {
        let title = this.taskTitleValidating();
        let backLogs = this.taskBacklogsValidating();
        let description = this.taskDescriptionValidating();
        let state = this.taskStateValidating();
        if (title && backLogs && description && state) {
            this.setState({loading: true});
            return fetch(`https://mamaly100.pythonanywhere.com/Task/`, {
                method: "POST",
                body: JSON.stringify({
                    "title": this.state.taskTitle,
                    "description": this.state.taskDescription,
                    "TaskState": this.state.taskState,
                    "picker": this.state.taskPicker,
                    "BackLogID": this.state.taskBacklog
                }),
                headers: {'Content-Type': 'application/json'},
            })
                .then(res => res.json())
                .then(data => {
                    this.setState({
                        loading: false
                    });
                    this.showSuccessMessage();
                })
                .catch(err => this.showErrorMassege())
        }

    };

    render() {
        return (
            <Form>
                <div className={"create-task-wrapper"}>
                    <Row className={"d-flex flex-row-reverse mb-4 align-items-center"}>
                        <Col span={4}>
                            <div className={"d-flex justify-content-end align-items-center"}>
                                <p className={"mr-2 fs-13"}>
                                    عنوان وظیفه
                                </p>
                                {/*<div className={"create-task-form-icon"}>*/}
                                {/*</div>*/}
                            </div>
                        </Col>
                        <Col span={8} className={"description-input-wrapper"}>
                            <Form.Item
                                hasFeedback
                                validateStatus={this.state.taskTitleValidating.validateStatus}
                                help={this.state.taskTitleValidating.help}
                                className={"m-0"}
                            >
                                <Input onChange={(e) => this.setState({taskTitle: e.target.value})}/>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row className={"d-flex flex-row-reverse mb-4 align-items-center"}>
                        <Col span={4}>
                            <div className={"d-flex justify-content-end align-items-center"}>
                                <p className={"mr-2 fs-13"}>
                                    بکلاگ
                                </p>
                                {/*<div className={"create-task-form-icon"}>*/}
                                {/*</div>*/}
                            </div>
                        </Col>
                        <Col span={8} className={"description-input-wrapper"}>
                            <Form.Item
                                hasFeedback
                                validateStatus={this.state.taskBacklogsValidating.validateStatus}
                                help={this.state.taskBacklogsValidating.help}
                                className={"m-0"}
                            >
                                <AutoComplete
                                    dataSource={this.state.dataSource}
                                    style={{width: '100%'}}
                                    onSelect={this.onSelect}
                                    onSearch={this.handleSearch}
                                    placeholder="بکلاگ مورد نظر را انتخاب کنید"
                                />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row className={"d-flex flex-row-reverse mb-4"}>
                        <Col span={4}>
                            <div className={"d-flex justify-content-end align-items-center"}>
                                <p className={"mr-2 fs-13"}>
                                    توصیفات وظیفه
                                </p>
                            </div>
                        </Col>
                        <Col span={8} className={"description-input-wrapper"}>
                            <Form.Item
                                hasFeedback
                                validateStatus={this.state.taskDescriptionValidating.validateStatus}
                                help={this.state.taskDescriptionValidating.help}
                                className={"m-0"}
                            >
                                <TextArea
                                    onChange={e => this.setState({taskDescription: e.target.value})}
                                    autosize={{minRows: 3, maxRows: 6}}
                                />
                            </Form.Item>
                        </Col>
                    </Row>
                    {/*<Row className={"d-flex flex-row-reverse mb-4 align-items-center"}>*/}
                    {/*<Col span={4}>*/}
                    {/*<div className={"d-flex justify-content-end align-items-center"}>*/}
                    {/*<p className={"mr-2 fs-13"}>*/}
                    {/*همکار*/}
                    {/*</p>*/}
                    {/*</div>*/}
                    {/*</Col>*/}
                    {/*<Col span={8} className={"description-input-wrapper"}>*/}
                    {/*<InputGroup compact>*/}
                    {/*<Select defaultValue="Select Collaborators..." style={{width: "100%"}}>*/}
                    {/*<Option value="Option1">Baqery</Option>*/}
                    {/*<Option value="Option2">Pazooki</Option>*/}
                    {/*<Option value="Option3">Babaei</Option>*/}
                    {/*<Option value="Option4">Sanei</Option>*/}
                    {/*</Select>*/}
                    {/*</InputGroup>*/}
                    {/*</Col>*/}
                    {/*</Row>*/}
                    <Row className={"d-flex flex-row-reverse mb-4 align-items-center"}>
                        <Col span={4}>
                            <div className={"d-flex justify-content-end align-items-center"}>
                                <p className={"mr-2 fs-13"}>
                                    وضعیت وظیفه
                                </p>
                            </div>
                        </Col>
                        <Col span={8} className={"description-input-wrapper"}>
                            <Form.Item
                                hasFeedback
                                validateStatus={this.state.taskStateValidating.validateStatus}
                                help={this.state.taskStateValidating.help}
                                className={"m-0"}
                            >
                                <InputGroup compact>
                                    <Select
                                        defaultValue={<span className={"fn-12"}>وضعیت وظیفه خود را انتخاب کنید</span>}
                                        onChange={e => this.setState({taskState: e})}
                                        style={{width: "100%"}} className={"custom-selection"}>
                                        <Option value="DONE" style={{fontSize: "12px", textAlign: "right"}}>انجام
                                            شده</Option>
                                        <Option value="TO_DO" style={{fontSize: "12px", textAlign: "right"}}>انجام
                                            نشده</Option>
                                        <Option value="IN_PROGRESS" style={{fontSize: "12px", textAlign: "right"}}>در
                                            جریان</Option>
                                    </Select>
                                </InputGroup>
                            </Form.Item>

                        </Col>
                    </Row>
                    <Row className={"d-flex flex-row-reverse mb-4 align-items-center"}>
                        <Col span={4}>

                        </Col>
                        <Col span={8} className={"description-input-wrapper"}>
                            <Button type="primary" onClick={this.checkValidating} loading={this.state.loading}>
                                اضافه کردن وظیفه
                            </Button>
                        </Col>
                    </Row>

                </div>
            </Form>
        )
    }
}

export default CreateTask;