import React, {Component} from "react";
import {withRouter} from 'react-router';
import {connect} from "react-redux";
import {Tabs} from 'antd';
import Tasks from "../components/Task/Tasks";
import CreateTask from "../components/Task/CreateTask";

const {TabPane} = Tabs;


class Task extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTabs: "7"
        }
    }

    tabsChange = (e) => {
        this.setState({
            activeTabs: e
        })
    };

    render() {
        return (
            <div className={"task-ui-wrapper"}>
                <div className={"task-ui-tabs-wrapper"}>
                    <div className={"task-ui-tabs"}>
                        <Tabs defaultActiveKey="7"
                              onChange={this.tabsChange}
                        >
                            <TabPane
                                className={"tab-pane-item"}
                                tab={
                                    <div className={"d-flex tabs-nav-item-wrapper align-items-center"}>
                                        <p>تمام وظایف</p>
                                        <div className={"tabs-nav-item-icon-all-task"}>

                                        </div>
                                    </div>
                                } key="6">
                                {this.state.activeTabs === "6" ? (<Tasks/>) : null}

                            </TabPane>
                            <TabPane
                                className={"tab-pane-item"}
                                tab={
                                    <div className={"d-flex tabs-nav-item-wrapper align-items-center"}>
                                        <p>ایجاد وظیفه جدید</p>
                                        <div className={"tabs-nav-item-icon-create-task"}>

                                        </div>
                                    </div>
                                } key="7">
                                {this.state.activeTabs === "7" ? (<CreateTask/>) : null}
                            </TabPane>
                        </Tabs>
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(Task);